// components/create-analysis-view.tsx
"use client"

import * as React from "react"
import { ArrowLeft, FileUp, Save } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Checkbox } from "@/components/ui/checkbox"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { useRouter } from "next/navigation"
import { useProject } from "@/hooks/use-projects"
import { useCreateAnalysis } from "@/hooks/use-analyses"
import { toast } from "@/hooks/use-toast"
import { Skeleton } from "@/components/ui/skeleton"
import type { Analysis } from "@/lib/services/analysis-service"

type CreateAnalysisViewProps = {
  projectId: string
}

export function CreateAnalysisView({ projectId }: CreateAnalysisViewProps) {
  const router = useRouter()
  const { data: project, isLoading: projectLoading } = useProject(projectId)
  const { mutate: createAnalysis, isPending } = useCreateAnalysis()
  
  const [isManualInput, setIsManualInput] = React.useState(true)
  const [formData, setFormData] = React.useState({
    title: "",
    description: "",
    type: "" as Analysis['type'] | "",
    context: "",
    technical: "",
    functional: "",
    stack: "",
    architecture: "",
    integrations: "",
    existingIssues: "",
    solutions: ""
  })

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!formData.title || !formData.type) {
      toast({
        title: "Champs requis",
        description: "Veuillez remplir le titre et le type d'analyse",
        variant: "destructive"
      })
      return
    }

    createAnalysis(
      {
        projectId,
        title: formData.title,
        description: formData.description || undefined,
        type: formData.type,
        content: {
          context: formData.context,
          technical: formData.technical,
          functional: formData.functional,
          stack: formData.stack,
          architecture: formData.architecture,
          integrations: formData.integrations || undefined,
          existingIssues: formData.existingIssues || undefined,
          solutions: formData.solutions || undefined
        }
      },
      {
        onSuccess: (analysisId) => {
          toast({
            title: "Analyse cre",
            description: "L'analyse a t cre avec succs"
          })
          router.push(`/project/${projectId}/analysis/${analysisId}`)
        },
        onError: (error) => {
          toast({
            title: "Erreur",
            description: error.message,
            variant: "destructive"
          })
        }
      }
    )
  }

  if (projectLoading) {
    return (
      <div className="space-y-6">
        <Skeleton className="h-10 w-96" />
        <Skeleton className="h-[600px]" />
      </div>
    )
  }

  if (!project) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="text-center space-y-4">
          <p className="text-muted-foreground">Projet introuvable</p>
          <Button onClick={() => router.back()}>Retour</Button>
        </div>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center gap-4">
        <Button
          variant="ghost"
          size="icon"
          onClick={() => router.back()}
          disabled={isPending}
        >
          <ArrowLeft className="h-5 w-5" />
        </Button>
        <div>
          <h1 className="text-3xl font-semibold tracking-tight text-foreground">
            Crer une Analyse
          </h1>
          <p className="text-sm text-muted-foreground mt-1">
            {project.name}
          </p>
        </div>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>Mode de Saisie</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="flex items-center gap-6">
            <div className="flex items-center space-x-2">
              <Checkbox 
                id="manual" 
                checked={isManualInput}
                onCheckedChange={(checked) => setIsManualInput(checked as boolean)}
              />
              <label
                htmlFor="manual"
                className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70"
              >
                Saisie manuelle
              </label>
            </div>
            <div className="flex items-center space-x-2">
              <Checkbox 
                id="import" 
                checked={!isManualInput}
                onCheckedChange={(checked) => setIsManualInput(!checked)}
              />
              <label
                htmlFor="import"
                className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70"
              >
                Importer un fichier
              </label>
            </div>
          </div>
        </CardContent>
      </Card>

      {isManualInput ? (
        <form onSubmit={handleSubmit} className="space-y-6">
          <Card>
            <CardHeader>
              <CardTitle>Informations Gnrales</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="grid gap-4 md:grid-cols-2">
                <div className="space-y-2">
                  <Label htmlFor="title">Titre de l'analyse *</Label>
                  <Input
                    id="title"
                    placeholder="Ex: Analyse technique du systme de paiement"
                    value={formData.title}
                    onChange={(e) => setFormData({ ...formData, title: e.target.value })}
                    required
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="type">Type d'analyse *</Label>
                  <Select
                    value={formData.type}
                    onValueChange={(value: Analysis['type']) => setFormData({ ...formData, type: value })}
                    required
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Slectionner un type" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="Technical">Technique</SelectItem>
                      <SelectItem value="Functional">Fonctionnelle</SelectItem>
                      <SelectItem value="UI/UX">UI/UX</SelectItem>
                      <SelectItem value="Performance">Performance</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
              </div>
              <div className="space-y-2">
                <Label htmlFor="description">Description</Label>
                <Textarea
                  id="description"
                  placeholder="Brve description de l'analyse..."
                  value={formData.description}
                  onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                  rows={2}
                />
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle>Contexte / Besoins Client</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-2">
                <Label htmlFor="context">
                  Dcrire le contexte du projet, les objectifs et les besoins du client
                </Label>
                <Textarea
                  id="context"
                  placeholder="Contexte du projet, objectifs, besoins du client..."
                  value={formData.context}
                  onChange={(e) => setFormData({ ...formData, context: e.target.value })}
                  rows={5}
                />
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle>Analyse Technique</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-2">
                <Label htmlFor="technical">
                  Contraintes, faisabilit, dfis techniques, dpendances
                </Label>
                <Textarea
                  id="technical"
                  placeholder="Contraintes techniques, faisabilit, dfis, dpendances..."
                  value={formData.technical}
                  onChange={(e) => setFormData({ ...formData, technical: e.target.value })}
                  rows={5}
                />
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle>Spcifications Fonctionnelles</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-2">
                <Label htmlFor="functional">
                  Fonctionnalits principales, cas d'usage cls
                </Label>
                <Textarea
                  id="functional"
                  placeholder="Fonctionnalits principales, cas d'usage..."
                  value={formData.functional}
                  onChange={(e) => setFormData({ ...formData, functional: e.target.value })}
                  rows={5}
                />
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle>Recommandation Stack Technique</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-2">
                <Label htmlFor="stack">
                  Technologies front-end, back-end, base de donnes, outils DevOps/CI-CD
                </Label>
                <Textarea
                  id="stack"
                  placeholder="Front-end: React, Next.js...
Back-end: Node.js, Python...
Base de donnes: PostgreSQL...
DevOps: Docker, GitHub Actions..."
                  value={formData.stack}
                  onChange={(e) => setFormData({ ...formData, stack: e.target.value })}
                  rows={6}
                />
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle>Architecture</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-2">
                <Label htmlFor="architecture">
                  Type d'architecture, composants, communication entre services
                </Label>
                <Textarea
                  id="architecture"
                  placeholder="Type: Microservices / Monolithique / Serverless...
Composants principaux...
Communication entre services..."
                  value={formData.architecture}
                  onChange={(e) => setFormData({ ...formData, architecture: e.target.value })}
                  rows={5}
                />
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle>Intgrations (optionnel)</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-2">
                <Label htmlFor="integrations">
                  API tierces, services externes
                </Label>
                <Textarea
                  id="integrations"
                  placeholder="API tierces (Stripe, SendGrid...)
Services externes (AWS S3, Cloudinary...)"
                  value={formData.integrations}
                  onChange={(e) => setFormData({ ...formData, integrations: e.target.value })}
                  rows={4}
                />
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle>Enjeux Existants (optionnel)</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-2">
                <Label htmlFor="existingIssues">
                  Bugs, problmes d'architecture, problmes de performance
                </Label>
                <Textarea
                  id="existingIssues"
                  placeholder="Bugs identifis...
Problmes d'architecture...
Problmes de performance..."
                  value={formData.existingIssues}
                  onChange={(e) => setFormData({ ...formData, existingIssues: e.target.value })}
                  rows={4}
                />
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle>Solutions Proposes (optionnel)</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-2">
                <Label htmlFor="solutions">
                  Solutions techniques, amliorations recommandes
                </Label>
                <Textarea
                  id="solutions"
                  placeholder="Solutions techniques proposes...
Amliorations recommandes...
Plan d'action..."
                  value={formData.solutions}
                  onChange={(e) => setFormData({ ...formData, solutions: e.target.value })}
                  rows={5}
                />
              </div>
            </CardContent>
          </Card>

          <div className="flex items-center gap-4">
            <Button
              type="button"
              variant="outline"
              onClick={() => router.back()}
              disabled={isPending}
            >
              Annuler
            </Button>
            <Button type="submit" disabled={isPending}>
              <Save className="mr-2 h-4 w-4" />
              {isPending ? "Cration..." : "Crer l'Analyse"}
            </Button>
          </div>
        </form>
      ) : (
        <Card>
          <CardContent className="flex items-center justify-center min-h-[400px]">
            <div className="text-center space-y-4">
              <FileUp className="h-16 w-16 mx-auto text-muted-foreground" />
              <p className="text-muted-foreground">
                Fonctionnalit d'import de fichier  venir
              </p>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  )
}